.container {
  width: 100%;
  height: 100vh;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn {
  width: 200px;
  height: 50px;
  border-radius: 20px;
  border: none;
  font-size: 20px;
  background-color: blue;
}
.btn:hover {
  transform: scale(1.1);
}
